* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  display: flex;
  align-items: center;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li {
  list-style-type: none;
  display: flex;
  align-items: center;
}
.first_string {
  display: flex;
  align-items: center;
}
.first_string > img {
  margin-right: 10px;
}
.list-item > img {
  margin-right: 10px;
  margin-left: 20px;
}
.list-item {
  height: 30px;
}

.list-item:not(:last-child) {
  margin-bottom: 10px;
}
.favourite {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-left: 50px;
}
.your__favourite {
  width: 30%;
  margin-left: auto;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: start;
  align-items: center;
  /* align-items: ; */
  /* position: fixed; */
  /* top: -90%; */
  /* left: ?>50%; */
}
.button__delete {
  margin-left: auto;
  margin-right: -10px;
}
.your__favourite-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.invis {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.form {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 528px;
  height: 581px;
  background: #fafafa;
  transform: translate(-50%, -50%);
  animation-name: changeModalBtn;
  animation-duration: 2000ms;
  z-index: 3;
}
